import React, { Component } from 'react';
import axios from 'axios';
import querystring from 'querystring';
import { contact_send } from './api/API';
import { Col, Container, Row, Button, Alert } from 'react-bootstrap';

class Contact extends Component {
    constructor(props) {
        super(props)
        this.state = {
            id: props.id, class: props.class,
            alert_visibility: false, alert_variant: '',
            alert_heading: '', alert_body: ''
        }
    }
    render() {
        return (
            <React.Fragment>
                <div id={this.state.id} className={this.state.class}>
                    <Container>
                        <Row>
                            <Col lg={6} md={6} sm={12} xs={12} className="justify-content-center align-self-center align-items-center wow fadeIn animated">
                                <div className="contact-information">
                                    <h2 className="h2-contact"><b>Contact</b></h2>
                                    <div className="div-border-contact"></div>
                                    <p className="contact-info">
                                        Ping me a message, ask me about something, book me an appointment or send me a direct message via email or Messenger.
                                    </p>
                                    <Button variant="outline-light" size="lg" className="btn-styled"
                                        onClick={()=>window.open('mailto:palisocnorman@gmail.com')} style={{marginTop: '5px'}}>
                                        Via Email
                                    </Button>&nbsp;&nbsp;&nbsp;
                                    <Button variant="outline-light" size="lg" className="btn-styled"
                                        onClick={()=>window.open('https://www.facebook.com/messages/t/blackhat1999', '_blank')} style={{marginTop: '5px'}}>
                                        Via Messenger
                                    </Button>  
                                </div>
                            </Col>
                            <Col lg={6} md={6} sm={12} xs={12} className="justify-content-center wow fadeIn animated">                                
                                <div className="contact-form">
                                    <Alert variant={this.state.alert_variant} show={this.state.alert_visibility} onClose={() => this.setState({alert_visibility: false})} dismissible>
                                        <Alert.Heading className="alert_heading">{this.state.alert_heading}</Alert.Heading>
                                        <p className="alert_body">
                                            {this.state.alert_body}
                                        </p>
                                    </Alert>
                                    <form method="POST">
                                        <div className="form-group">
                                            <label htmlFor="fullname" className="lbl-name">Full Name</label>
                                            <input type="text" name="full_name" className="form-control form-styled" id="fullname" required="required" placeholder="e.g. Juan Dela Cruz ..." />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="email" className="lbl-name">Email Address</label>
                                            <input type="email" name="email_address" className="form-control form-styled" id="email" required="required" placeholder="e.g. juandelacruz@gmail.com ..." />
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="desc" className="lbl-name">Description</label>
                                            <textarea className="form-control form-styled" id="description" name="description" rows="4" required="required" placeholder="e.g. Appointments, Hiring, Collaborations etc."></textarea>
                                        </div>
                                        <Button variant="success" id="btnSubmit" size="lg" type="button" className="btn-styled" onClick={() => {                                            
                                            const fn = document.getElementById('fullname');const em = document.getElementById('email');const de = document.getElementById('description');const sb = document.getElementById('btnSubmit');const rs = document.getElementById('btnReset');                                            
                                            const datetime = new Date().toLocaleString();
                                            if (fn.value === '' || em.value === '' || de.value === '') { window.alert('Please Fill Up All Fields Before Submitting'); return;}
                                            fn.setAttribute('disabled', 'disabled');em.setAttribute('disabled', 'disabled');de.setAttribute('disabled', 'disabled');sb.setAttribute('disabled', 'disabled');rs.setAttribute('disabled', 'disabled');
                                            axios({method: 'POST', url: `${contact_send}`,
                                                data: querystring.stringify({full_name: fn.value, email_address: em.value, description: de.value, date_sent: datetime}),
                                                headers: {'content-type': 'application/x-www-form-urlencoded;charset=utf-8'}
                                            }).then((response) => {
                                                this.setState({alert_variant: response.data.variant})
                                                this.setState({alert_visibility: true})
                                                this.setState({alert_heading: response.data.message})
                                                this.setState({alert_body: response.data.body})
                                                fn.removeAttribute('disabled', 'disabled');em.removeAttribute('disabled', 'disabled');de.removeAttribute('disabled', 'disabled');sb.removeAttribute('disabled', 'disabled');rs.removeAttribute('disabled', 'disabled');fn.value = ''; em.value = ''; de.value = '';
                                            }, (error) => {
                                                console.log(error)
                                                fn.removeAttribute('disabled', 'disabled');em.removeAttribute('disabled', 'disabled');de.removeAttribute('disabled', 'disabled');sb.removeAttribute('disabled', 'disabled');rs.removeAttribute('disabled', 'disabled');
                                            });
                                        }}>
                                            Submit
                                        </Button>&nbsp;&nbsp;&nbsp;
                                        <Button variant="danger" id="btnReset" size="lg" type="reset" className="btn-styled">
                                            Reset
                                        </Button>
                                    </form>
                                </div>                                
                            </Col>
                        </Row>
                    </Container>
                </div>
            </React.Fragment>
        )
    }
}

export default Contact;