import React, { Component } from 'react';
import { Container, Row, Col } from 'react-bootstrap';

class Education extends Component {
    constructor(props) {
        super(props)
        this.state = { id: props.id, class: props.class }
    }
    render () {
        return (
            <React.Fragment>
                <div id={this.state.id} className={this.state.class}>
                    <h2 className="h2-educ-title wow fadeIn animated"><b>Educational</b> Background</h2>
                    <div className="div-border-educ wow fadeIn animated"></div>
                    <Container>
                        <div className="bs-stepper linear wow fadeIn animated">
                            <div className="bs-stepper-header" role="tablist">
                                <div className="step active stepper-bg" data-target="#tertiary">
                                    <button type="button" className="step-trigger" role="tab" aria-controls="tertiary" id="tertiary-trigger">
                                        <span className="bs-stepper-circle">
                                            <i className="fa fa-graduation-cap"></i>
                                        </span>
                                        <span className="bs-stepper-label">
                                            Tertiary<br/>
                                            <b className="stepper-months">March 2020</b>
                                        </span>
                                    </button>
                                </div>
                                <div className="line"></div>
                                <div className="step stepper-bg" data-target="#secondary">
                                    <button type="button" className="step-trigger" role="tab" aria-controls="secondary" id="secondary-trigger">
                                        <span className="bs-stepper-circle">
                                            <i className="fa fa-graduation-cap"></i>
                                        </span>
                                        <span className="bs-stepper-label">
                                            Secondary<br/>
                                            <b className="stepper-months">April 2015</b>
                                        </span>
                                    </button>
                                </div>
                                <div className="line"></div>
                                <div className="step stepper-bg" data-target="#elementary">
                                    <button type="button" className="step-trigger" role="tab" aria-controls="elementary" id="elementary-trigger">
                                        <span className="bs-stepper-circle">
                                            <i className="fa fa-graduation-cap"></i>
                                        </span>
                                        <span className="bs-stepper-label">
                                            Elementary<br/>
                                            <b className="stepper-months">March 2011</b>
                                        </span>
                                    </button>
                                </div>
                            </div>
                            <div className="bs-stepper-content wow fadeIn animated">
                                <Row>
                                    <Col lg={4} md={4} sm={4} xs={12}>
                                        <div className="educ-content">
                                            <h2 className="h2-educ-content-title">PHINMA University of Pangasinan</h2>
                                            <p className="p-educ-content-desc">Bachelor of Science in Information Technology Education</p>
                                        </div>
                                    </Col>
                                    <Col lg={4} md={4} sm={4} xs={12}>
                                        <div className="educ-content">
                                            <h2 className="h2-educ-content-title">Payas National High School</h2>
                                            <p className="p-educ-content-desc">4th Honorable Mention<br/>High School Diploma</p>
                                        </div>
                                    </Col>
                                    <Col lg={4} md={4} sm={4} xs={12}>
                                        <div className="educ-content">
                                            <h2 className="h2-educ-content-title">Cablong Elementary School</h2>
                                            <p className="p-educ-content-desc">2nd Honorable Mention<br/>Elementary Diploma</p>
                                        </div>
                                    </Col>
                                </Row>                                
                            </div>
                        </div>
                    </Container>
                </div>                
            </React.Fragment>
        )
    }
}

export default Education;