import React, { Component } from 'react';
import { Container, Row, Col, Button } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { SkillsItems } from './vars/ContainerVars';

class Skills extends Component {
    constructor(props) {
        super(props)
        this.state = { id: props.id, class: props.class }
    }
    render () {
        return (
            <React.Fragment>
                <div id={this.state.id} className={this.state.class}>
                    <h2 className="h2-st-title wow fadeIn animated">Skills and <b>Technologies</b></h2>
                    <div className="div-border-st wow fadeIn animated"></div>
                    <p className="p-skills-desc wow fadeIn animated">
                        Programming Languages, Tools and Trending Technologies I know.<br/>                       
                    </p>
                    <br/>
                    <Container>
                        {SkillsItems.map((skills, i) =>
                            <Row key={i}>
                                {skills.cols.map((cols, i) =>
                                    <React.Fragment key={i}>
                                        <Col lg={cols.lg} md={cols.md} sm={cols.sm} xs={cols.xs} className="justify-content-center wow zoomIn animated">
                                            <div className="div-icons">
                                                {cols.fonts.map((icons, i) =>
                                                    <React.Fragment key={i}>
                                                        <FontAwesomeIcon icon={icons} className={cols.fontsClassName}/>&nbsp;&nbsp;
                                                    </React.Fragment>
                                                )}
                                            </div>
                                            <h3 className="skills-title">{cols.skillsTitle}</h3>
                                            <p className="skills-desc">{cols.skillsDesc}</p>
                                            <div className="div-skills">
                                                {cols.skills.map((skill, i) =>
                                                    <React.Fragment key={i}>
                                                        <Button variant={cols.variant} size="sm" className={cols.className}>{skill}</Button>&nbsp;&nbsp;
                                                    </React.Fragment>
                                                )}
                                            </div>
                                            <br/><br/>
                                        </Col>
                                    </React.Fragment>
                                )}
                            </Row>                                
                        )}
                    </Container>
                </div>
            </React.Fragment>
        )
    }
}

export default Skills;