import React, { Component } from 'react';
import { StepperHeaderList, StepperContentList } from './vars/ContainerVars';
import { Container } from 'react-bootstrap';

class Experience extends Component {
    constructor(props) {
        super(props)
        this.state = { id: props.id, class: props.class }
    }
    render () {
        return (
            <div id={this.state.id} className={this.state.class}>
                <Container>
                    <h2 className="h2-we-title">Work-Related <b>Experience</b></h2>
                    <div className="div-border-we"></div>
                    <div id="wRE" className="bs-stepper vertical">
                        <div className="bs-stepper-header" role="tablist">
                            {
                                StepperHeaderList.map((links, i) =>
                                    <React.Fragment key={i}>
                                        <div className="step active stepper-bg wow fadeIn animated" data-target={links.dataTarget} key={i}>
                                            <button type="button" className="step-trigger" role="tab" aria-controls={links.ariaControls} id={links.id} key={i}>
                                                <span className="bs-stepper-circle">
                                                    <i className="fa fa-briefcase"></i>
                                                </span>
                                                <span className="bs-stepper-label">
                                                    {links.jobRange}<br/>
                                                    <b className="stepper-months">{links.months}</b>
                                                </span>
                                            </button>
                                        </div>
                                        <div className={links.line}></div>
                                    </React.Fragment>
                                )
                            }
                        </div>
                        <div className="bs-stepper-content">
                            {
                                StepperContentList.map((links, i) => 
                                    <React.Fragment key={i}>
                                        <div id={links.id} className="content active wow fadeIn animated" role="tabpanel" aria-labelledby={links.ariaLabelledBy} key={i}>
                                            <div className="we-content" key={i}>
                                                <h2 className="h2-we-content-title"><b>{links.contentTitle}</b></h2>
                                                <h3 className="h3-we-content-location"><u><a href={links.href} target="_blank" rel="noreferrer noopener" style={{color: '#000'}}>{links.company}</a></u> | {links.location}</h3>
                                                <div className="div-we-content-task" key={i}>
                                                    <ul className="list-group list-group-flush">
                                                        {links.tasks.map((task, i) => <li key={i} className="list-group-item item-c"><i className="fa fa-check-square"></i>&nbsp;&nbsp;{task}</li>)}
                                                    </ul>
                                                </div>
                                            </div>
                                        </div>
                                    </React.Fragment>
                                )
                            }
                        </div>
                    </div>
                </Container>
            </div>
        )
    }
}

export default Experience;