import React, { Component } from 'react';
import { Container, Row, Col, Button } from 'react-bootstrap';
import { PortfolioItems, PortfolioModals } from './vars/ContainerVars.js';
import '../assets/container.css';

class Projects extends Component {
    constructor(props) {
        super(props)
        this.state = { id: props.id, class: props.class }
    }
    render () {
        return (
            <React.Fragment>
                <div id={this.state.id} className={this.state.class}>
                    <Container>
                        <h2 className="h2-proj-title wow fadeIn animated"><b>Projects</b> I've Developed</h2>
                        <div className="div-border-proj wow fadeIn animated"></div>
                        <p className="p-proj-desc wow fadeIn animated">Below are some of the projects I have done by myself, in my entire experience as a Developer.<br/>Some are currently on progress.</p>
                        <br/>
                        <section id="portfolio" className="wow fadeIn animated">
                            <div className="gallary animate-grid">
                                <Container>
                                    <Row>
                                        <Col lg={12} md={12} sm={12} xs={12}>
                                            <div className="categories">
                                                <ul><li><ol>
                                                    <li className="no-mleft wow zoomIn animated"><a href="#" data-filter="*" className="active">All Projects</a></li>
                                                    <li className="wow zoomIn animated"><a href="#" data-filter=".ongoing">Ongoing</a></li>
                                                    <li className="wow zoomIn animated"><a href="#" data-filter=".web">Web Apps</a></li>
                                                    <li className="wow zoomIn animated"><a href="#" data-filter=".mobile">Mobile Apps</a></li>
                                                    <li className="wow zoomIn animated"><a href="#" data-filter=".softwares">Software | Others</a></li>
                                                </ol></li></ul>
                                            </div>
                                        </Col>
                                    </Row>
                                    <Row className="gallary-thumbs" style={{marginTop: '-30px'}}>
                                        {
                                            PortfolioItems.map((items, i) =>
                                                <React.Fragment key={i}>
                                                    <Col lg={items.lg} md={items.md} sm={items.sm} xs={items.xs} className={items.className}>
                                                        <a href={items.hrefID} className="portfolio-link" data-toggle="modal">
                                                            <div className="portfolio-hover" id="img-hover">
                                                                <div className="portfolio-hover-content">
                                                                    <i className="fa fa-search-plus zoomS"></i>
                                                                </div>
                                                            </div>
                                                            <img src={items.img} id="img-project" className="img-fluid" alt={items.alt} />
                                                        </a> 
                                                        <div className="portfolio-caption">
                                                            <h4 className="h4-portfolio-title"><b>{items.title}</b></h4>
                                                            <p className="p-portfolio-language">{items.lang}</p>
                                                            <p className="p-portfolio-content">
                                                                {items.content}
                                                            </p>
                                                            <Button variant={items.variant} size="sm" className="btn-status" data-toggle="tooltip" title={items.tooltip}>
                                                                {
                                                                    items.platform.map((plt, i) => 
                                                                        <React.Fragment key={i}>
                                                                            <i className={plt}></i>&nbsp;&nbsp;
                                                                        </React.Fragment>
                                                                    )
                                                                }
                                                                {items.status}
                                                            </Button>
                                                        </div>
                                                    </Col>
                                                </React.Fragment>
                                            )
                                        }
                                    </Row>
                                </Container>
                            </div>
                        </section>
                        {
                            PortfolioModals.map((modals, i) => 
                                <React.Fragment key={i}>
                                    <div className="portfolio-modal modal fade" id={modals.id} tabIndex="-1" role="dialog" aria-hidden="true" style={{display: 'none'}}>
                                        <div className="modal-dialog">
                                            <div className="modal-content">
                                                <div className="close-modal" data-dismiss="modal">
                                                    <div className="lr">
                                                        <div className="rl">
                                                        </div>
                                                    </div>
                                                </div>
                                                <Container>
                                                    <Row style={{justifyContent: 'center'}}>
                                                        <Col lg={8}>
                                                            <div className="modal-body"><img className="img-responsive img-fluid img-centered" src={modals.img} alt={modals.alt} /></div>
                                                        </Col>
                                                    </Row>
                                                </Container>
                                            </div>
                                        </div>
                                    </div>
                                </React.Fragment>
                            )
                        }                        
                    </Container>                    
                </div>
            </React.Fragment>
        )
    }    
}

export default Projects;