import React, { Component } from 'react';
import { Container, Row, Col } from 'react-bootstrap';

import impact_hub from '../images/impact_hub.jpg';
import laravel_philippines from '../images/laravel_philippines.jpg';
import ai_pilipinas from '../images/ai_pilipinas.jpg';
import python_philippines from '../images/python_philippines.jpg';
import tensorflow_developers from '../images/tensorflow_developers.jpg';
import vue_js_philippines from '../images/vue_js_philippines.jpg';

class Affiliations extends Component {
    constructor(props) {
        super(props)
        this.state = { id: props.id, class: props.class }
    }
    render() {
        return (
            <React.Fragment>
                <div id={this.state.id} className={this.state.class}>
                    <Container>
                        <h2 className="h2-aff-title wow fadeIn animated"><b>Affiliations</b></h2>
                        <div className="div-border-aff wow fadeIn animated"></div>
                        <p className="p-acc-desc wow fadeIn animated">
                            Organizations, Connections and Groups I have joined and occasionally participating
                        </p><br/>
                        <Container>
                            <Row>
                                <Col lg={2} md={3} sm={4} xs={4} className="justify-content-center col-padding wow zoomIn animated">
                                    <img src={impact_hub} className="img-fuild img-aff-circle" alt="impact_hub"/>
                                    <h2 className="h2-aff-name">Impact Hub Manila</h2>
                                </Col>
                                <Col lg={2} md={3} sm={4} xs={4} className="justify-content-center col-padding wow zoomIn animated">
                                    <img src={laravel_philippines} className="img-fuild img-aff-circle" alt="laravel_philippines"/>
                                    <h2 className="h2-aff-name">Laravel Philippines</h2>
                                </Col>
                                <Col lg={2} md={3} sm={4} xs={4} className="justify-content-center col-padding wow zoomIn animated">
                                    <img src={ai_pilipinas} className="img-fuild img-aff-circle" alt="ai_pilipinas"/>
                                    <h2 className="h2-aff-name">AI Pilipinas</h2>
                                </Col>
                                <Col lg={2} md={3} sm={4} xs={4} className="justify-content-center col-padding wow zoomIn animated">
                                    <img src={python_philippines} className="img-fuild img-aff-circle" alt="python_philippines"/>
                                    <h2 className="h2-aff-name">Python Philippines</h2>
                                </Col>
                                <Col lg={2} md={3} sm={4} xs={4} className="justify-content-center col-padding wow zoomIn animated">
                                    <img src={tensorflow_developers} className="img-fuild img-aff-circle" alt="tensorflow_developers"/>
                                    <h2 className="h2-aff-name">Tensorflow Developers</h2>
                                </Col>
                                <Col lg={2} md={3} sm={4} xs={4} className="justify-content-center col-padding wow zoomIn animated">
                                    <img src={vue_js_philippines} className="img-fuild img-aff-circle" alt="vue_js_philippines"/>
                                    <h2 className="h2-aff-name">Vue JS Philippines</h2>
                                </Col>
                            </Row>
                        </Container>
                    </Container>                    
                </div>
            </React.Fragment>
        )
    }
}

export default Affiliations;